import { Component, Prop, Event, EventEmitter, h, Host } from '@stencil/core';

@Component({
  tag: 'bh-modal-header',
  styleUrl: 'bh-modal-header.css',
  shadow: true,
})
export class BhModalHeader {
  /**
   * Modal title (required)
   */
  @Prop() modalTitle!: string;

  /**
   * Modal description/supporting text
   */
  @Prop() description?: string;

  /**
   * Icon name for the featured icon (Material Symbols)
   */
  @Prop() icon?: string;

  /**
   * Color theme for the featured icon
   */
  @Prop() iconColor:
    | 'brand'
    | 'gray'
    | 'error'
    | 'warning'
    | 'success'
    | 'highlight' = 'success';

  /**
   * Whether to show the featured icon
   */
  @Prop() showIcon: boolean = true;

  /**
   * Whether to show the close button
   */
  @Prop() showCloseButton: boolean = true;

  /**
   * Header alignment type
   */
  @Prop() type: 'left-aligned' | 'center-aligned' | 'horizontal-left-aligned' =
    'left-aligned';

  /**
   * Whether to show padding at the bottom
   */
  @Prop() paddingBottom: boolean = true;

  /**
   * Whether to show the divider line
   */
  @Prop() divider: boolean = false;

  /**
   * Emitted when the close button is clicked
   */
  @Event() bhClose!: EventEmitter<void>;

  private handleClose = (): void => {
    this.bhClose.emit();
  };

  render() {
    const isHorizontal = this.type === 'horizontal-left-aligned';
    const isCenter = this.type === 'center-aligned';

    return (
      <Host>
        <div
          class={{
            'modal-header': true,
            'modal-header--center': isCenter,
            'modal-header--horizontal': isHorizontal,
          }}
        >
          <div
            class={{
              'modal-header-content': true,
              'modal-header-content--center': isCenter,
              'modal-header-content--horizontal': isHorizontal,
            }}
          >
            {this.showIcon && this.icon && (
              <bh-featured-icon
                icon={this.icon}
                color={this.iconColor}
                size="md"
                iconStyle="outlined"
              />
            )}

            <div
              class={{
                'modal-text-content': true,
                'modal-text-content--center': isCenter,
                'modal-text-content--horizontal': isHorizontal,
              }}
            >
              <h3 class="modal-title">{this.modalTitle}</h3>
              {this.description && (
                <p class="modal-description">{this.description}</p>
              )}
            </div>
          </div>

          {this.showCloseButton && (
            <bh-button-icon
              class="close-button"
              hierarchy="quaternary"
              size="sm"
              iconName="close"
              ariaLabel="Close modal"
              onBhClick={this.handleClose}
            />
          )}
        </div>

        {this.paddingBottom && <div class="padding-bottom" />}

        {this.divider && <div class="divider" />}
      </Host>
    );
  }
}
